import React, { Component } from 'react';
import { Server } from '../../services';
import { Card, Container } from "react-bootstrap";
import DataTable from './DataTable'
import Search from './Search'

const server = new Server('/buyer/')


class Billing extends Component {
    state = {
        list: [],
        buyer: {}
    }

    componentDidMount() {
        this.getData()
    }
    getData = async () => {
        this.setState({
            isLoading: true
        })

        try {
            let { data: { results } } = await server.get('/billing/')
            let buyer = await server.get()

            this.setState({
                list: results,
                buyer: buyer.data.results[0] || {},
                isLoading: false
            })
        } catch (error) {
            console.error(error)
        }
    }




    render() {
        let { list, isLoading, buyer } = this.state

        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <h5><u>পেমেন্ট লিস্ট</u></h5>
                    </Card.Header>
                    <Card.Body>

                        <Search
                            getData={this.getData}
                            setStateHandler={(loading, value) => this.setState({ list: value, isLoading: loading })}
                        />

                        <DataTable
                            list={list}
                            buyer={buyer}
                            isLoading={isLoading}
                            paymentHandler={this.paymentHandler}
                            getData={this.getData}
                        />
                    </Card.Body>
                </Card>
            </Container>
        );
    }
}

export default Billing;
